<template>
    <div class="science">
        <div class="title-content clearfix">
            <div class="title-box fl">
                <div class="t-text">科技>>></div>
            </div>
            <div class="line-box fl"></div>
            <div class="line-box fl"></div>
            <div class="ad-img-box fr">
                <a target="_blank" href="http://tv.cctv.com/lm/kjy/">
                    <img class="ad-img auto-img" src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3466042216,2612565689&fm=26&gp=0.jpg" border="0"  alt="">
                </a>
            </div>
        </div>

        <div class="cart-content">
            <div class="cart-box clearfix">
                <div class="c-item fl" v-for="(item,index) in ScienceData.slice(0,6)" :key="index" @click="goDetail(item.id)">
                    <img class="imgs" :src="item.images" alt="">
                    <div class="text">{{item.title}}</div>
                </div>
            </div>
        </div>

        <div class="newList-content">
            <div class="clearfix">
                <div class="new-list-left-box fl">
                     <div class="list-item clearfix" v-for="(item,index) in ScienceData" :key="index" @click="goDetail(item.id)">
                        <div class="left-img fl">
                            <el-image class=" err-imgs">
                                <div slot="error" class="image-slot">
                                    <i class="el-icon-picture-outline"></i>
                                </div>
                            </el-image>
                            <img class="a-img auto-img" :src="item.images" alt="">
                        </div>
                        <div class="right-cont fl">
                            <div class="title">{{item.title}}</div>
                            <div class="text-cont">
                                <span class="post-name">{{item.posterNickName}}</span>
                                <span class="pub-time">日期：{{item.publishTime}}</span>
                                <span class="comment-count">评论：{{item.commentCount}}</span>
                            </div>
                        </div>
                    </div>

                     <div class="pagination-page">
                        <el-pagination 
                        class="pag-box" 
                        background 
                        layout="prev, pager, next" 
                        :size-change="10"
                        :total="1000"
                        :current-change = "currentPage"
                        @current-change="handleCurrentChange"
                        >
                        </el-pagination>
                    </div>
                   
                </div>

                <div class="new-other-right-box fl">
                    <div class="other-list">
                        <div class="other-box-one">
                            <div class="other-title">
                                <div class="ot-name">手机资讯</div>
                            </div>
                            <div class="other-cont clearfix" v-for="(item,index) in PhoneData" :key="index" @click="goDetail(item.id)">
                                <div class="left fl">
                                   <div class="le">
                                        <div class="title">{{item.title}}</div>
                                        <div class="post-name">{{item.posterNickName}}</div>
                                   </div>
                                </div>
                                <div class="right fl">
                                    <img :src="item.images" alt="">
                                </div>
                            </div>
                        </div>
                        <!-- <div class="other-box-two">
                            <div class="other-title">
                                <div class="ot-name">数码资讯</div>
                            </div>
                            <div class="other-cont" v-for="(item,index) in CameraData" :key="index" @click="goDetail(item.id)">
                                <div class="cont-img">
                                    <img :src="item.images" alt="">
                                </div>
                                <div class="cont-text">
                                    <div class="title">{{item.title}}</div>
                                </div>
                            </div>
                        </div> -->
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import "../../assets/css/science.less";
    export default {
        name:'science',
        data(){
            return{
                currentPage:1,
                // 定义页数
                pageNum:'',
                ScienceData:[],
                PhoneData:[],
                CameraData:[],
            }
        },
        created(){
            this.getScienceData()
            this.getPhoneData()
            // this.getCameraData()
        },
        methods:{

            getScienceData(pageNum){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1348649580692',
                        page:pageNum,
                    }
                }).then((result) =>{
                    // console.log('result',result)
                    this.ScienceData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },
             //点击页码，获取对应的val值，在传给要显示的页数内容
            handleCurrentChange(val){
                
                // console.log('val',val)
                let pageNum = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getScienceData(pageNum)
            
            },

             getPhoneData(){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1348649654285',
                        page:1,
                    }
                }).then((result) =>{
                    // console.log('result',result)
                    this.PhoneData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },

            // getCameraData(){
            //     this.axios({
            //         method:'GET',
            //         url:'/list',
            //         params:{
            //             apikey: this.appkey,
            //             category:'T1348649776727',
            //             page:1,
            //         }
            //     }).then((result) =>{
            //         console.log('result',result)
            //         this.CameraData = result.data.data
            //     }).catch((err) =>{
            //         console.log('err',err)
            //     })
            // },
            goDetail(id) {
                // console.log(id);
                this.$router.push({ name: "Detail", query: { id } });
            },
        }
    }
</script>

<style lang="less" scoped>

</style>